import { useState } from "react";

import { FaSearch } from "react-icons/fa";

import { Link } from "react-router-dom";
import ClassName from "classnames";
import HeaderItem from "./HeaderItem";
import "./Header.css";

function Header() {
  const [state, setState] = useState({
    select: 0,
    isFocus: false,
    list: [
      {
        name: "Trang chủ",
        href: "/"
      },
      {
        name: "API",
        href: "/api"
      }
    ]
  });

  function setSelect(index) {
    setState({
      ...state,
      select: index
    });
  }

  const setFocus = function () {
    setState({
      ...state,
      isFocus: true
    });
  };

  const setBlur = function () {
    setState({
      ...state,
      isFocus: false
    });
  };

  return (
    <div className="Header">
      <div className="max-800">
        <div
          className={ClassName({
            HeaderContainer: true,
            SearchFocus: state.isFocus
          })}
        >
          <div className="HeaderMenuItem">
            {state.list.map((item, index) => {
              return (
                <HeaderItem
                  key={index}
                  index={index}
                  href={item.href}
                  isActive={state.select === index}
                  event={setSelect}
                >
                  {item.name}
                </HeaderItem>
              );
            })}
          </div>

          <label
            className={ClassName({
              HeaderSearch: true
            })}
            htmlFor="searchElement"
            onClick={setFocus}
          >
            <FaSearch />
            <input
              id="searchElement"
              type="text"
              onBlur={setBlur}
              placeholder="Tìm kiếm mọi thứ tại đây"
            />
          </label>

          <div className="HeaderMenuItem">
            <a href="http://localhost" target="_blank" className="HeaderItem">
              Thử Ngay
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Header;
